
import PostCard, { Comment as PostCardComment } from "@/components/PostCard";
const mockComments: PostCardComment[] = [
    { id: 1, user: "Alice", text: "Great post!" },
    { id: 2, user: "Bob", text: "Thanks for sharing." },
    { id: 3, user: "Charlie", text: "Interesting insights." },
];

export default function vizualisation() {
    return (
        <PostCard
            companyName="Acme Corp"
            postTime="2 hours ago"
            introText="This is an introductory text for the post. It gives a brief overview or context."
            likes={12}
            commentsCount={mockComments.length}
            comments={mockComments}
            // Optionally add handlers for like, comment, share, addComment
        />
    );
}